export default {
  name: "theav",
  data() {
    return {
      download_data: undefined,
      topic_data: undefined
    };
  },
  created() {
    this.id = this.$route.params.id;
    console.log('this.id:', this.id);
    this.topic_data = this.get_topic_data_from_local(this.id);
    console.log('[theav]topic_data:', this.topic_data);
    this.update_topic_data(this.topic_data);
    this.load_download_data();
  },
  methods: {
    update_topic_data: function (topic_data) {
      if (topic_data) {
        this.topic_data = topic_data;
        this.set_title(this.topic_data.title + "视频下载");
      }
    },
    load_download_data: function () {
      this.$axios.get('/app/download/theav', {
        params: {
          id: this.id,
        }
      }).then((response) => {
        var download_data = response.data;
        console.log("download_data:", download_data);
        this.download_data = download_data;
        this.update_topic_data(download_data.topic_data);
      }).catch((error) => {
        console.log("error:", error);
        this.loading = false;
      });
    }
  }
}
